// text-color
$text-color: #848789;
$title-color: #353738;
$nav-color: #4d4959;
// layout color
$accent: #64c29e;
$grey: #f7f7f7;
$dark: #282f35;

@import "~normalize.css";
@import "./fonts";
html {
  scroll-behavior: smooth;
}
body {
  font-size: 16px;
  font-family: "Lato", sans-serif;
  text-rendering: optimizeLegibility;
  -moz-osx-font-smoothing: grayscale;
  -moz-font-feature-settings: "liga" on;
  line-height: 1.65;
}
